







































import Vue from 'vue';
import { Data, Methods, Computed, Props } from './types';
import MIcon from '@/components/MIcon/MIcon.vue';
import MPanel from '@/components/MPanel/MPanel.vue';
export default Vue.extend<Data, Methods, Computed, Props>({
  components: {
    MIcon,
    MPanel
  },
  props: {
    type: {
      type: String,
      default: ''
    },
    open: {
      type: Boolean,
      default: false
    },
    toggle: {
      type: Function,
      default: null
    }
  },
  data() {
    return {
      dataOpen: this.open
    };
  },
  computed: {
    blockName() {
      return this.type ? `m-accordion-${this.type}` : 'm-accordion';
    }
  },
  watch: {
    open(newValue) {
      this.dataOpen = newValue;
    }
  },
  methods: {
    toggleDataOpen() {
      this.toggle ? this.toggle() : (this.dataOpen = !this.dataOpen);
    },
    beforeEnter(element) {
      element.style.height = '0';
    },
    enter(element) {
      element.style.height = `${element.scrollHeight}px`;
    },
    beforeLeave(element) {
      element.style.height = `${element.scrollHeight}px`;
    },
    leave(element) {
      element.style.height = '0';
    }
  }
});
